import React, { useState } from 'react'
import './formpage.css'
import Button from '../../button/button'
import image from '../Images/two-business-partners-working-together (1).jpg'

const array = [
    {
        text: 'Name'
    },
    {
        text: 'Company'
    },
    {
        text: 'email'
    },
    {
        text: 'Phone Number'
    },
    {
        text: 'message'
    }
]

const FormPage = () => {

  return (
    <div className='form-page'>
        <div className="content-wrap">
            <div className="welcome">
                <h1>We welcome<br/>your feedback.</h1>
                <div className="mobile-form-photo">
                    <h2>hello@pixelflakes.com</h2>
                    <h2>+44 (0)20 7112 9355</h2>
                    <img src={image} alt="" />
                </div>
                <div className="welcome-para">
                    <p>Pixelflakes was established with</p>
                    <p>he aim of producing work that we take pride in. If you resonate with this vision and wish to explore potential collaborations, we invite you to reach out for more information.</p>
                </div>
            </div>
            <form action="">
                {
                    array.map( (elem,i) => {
                        return (
                            <Input name={elem.text} key={i}/>
                        )
                    } )
                }
                <button>
                    <Button textColor={'#121212'} title={'Send Message'} lineColor={'#121212'} initial={'#fff'} up={'#fff'} down={'#121212'} invert={0.07} borderColor={'#12121286'} fontFamily={'a2'} fontWeight={'900'} letterSpacing={'0.4px'} />
                </button>
            </form>
        </div>
        <div className="vignette">
              <img src={image} alt="" style={{width:'100%', height:"100%", objectFit:'cover'}} />
        </div>
        <div className="fixed-contact form-fixed-contact">
            <a href="">hello@pixelflakes.com</a>
            <a href="">+44 (0)20 7112 9355</a>
        </div>
    </div>
  )
}

export default FormPage


const Input = ({name}) => {
    
    const [value, setValue] = useState('')

    const handleChange = (e) => {
        setValue(e.target.value)
    }
    return (
        <div className="input-wrap">
            <h2>{name}</h2>
            <input type="text" value={value} onChange={handleChange}/>
        </div>
    )
}