import React, { useRef, useState } from 'react'
import Transition from './transition/transition'
import gsap from 'gsap'
import { useEffect } from 'react'
import FooterMenu from './footerMenu/footerMenu'
import FooterBar from './footerBar/footerBar'
import './contact/contact.css'
import FormPage from './contact/FormPage/FormPage'
import TextBlock from './textBlock/TextBlock'
import image1 from './contact/Images/handsome-businessman-working-office (1).jpg'
import image2 from './contact/Images/empty-business-meeting-conference-room-with-graphs-diagrams-tv-background_482257-25767 (1).jpg'
import { motion } from 'framer-motion'
import rightArr from '../Images/right-up.png'

 
function Contact() {
  return (
    <Transition title={'Contact'}>
      <main id="main">
        <div className="contactPage">
          <FormPage/>
          <Locations/>
          <div className="faltu">
            <FooterMenu/>
            <FooterBar/>
          </div>
        </div>
      </main>
    </Transition>
  )
}

export default Contact

const locationsArray = [
  {
    text: 'London',
    flag: false,

  },
  {
    text: 'Amsterdam',
    flag: true
  }
]

const locationsMobile = [
  {
    title: 'London',
    img: image1,
    flag: false,
    address: {
      street:'Unit 1, 141 Mare Street',
      area: 'E8 3RH London',
      city:'United Kingdom',
    },
    links: {
      email: 'hello@pixelflakes.com',
      phone: '+44 (0)20 7112 9355'
    }
  },

  {
    title: 'Amsterdam',
    img: image2,
    flag: true,
    address: {
      street:'Geerdinkhof 247',
      area: '1103 RA Amsterdam',
      city:'The Netherlands',
    },
    links: {
      email: 'hello@pixelflakes.com',
      phone: '+44 (0)20 7112 9355'
    }
  }
]


const vimImages = [
  {
    img:image1,
    flag:true
  },
  {
    img:image2,
    flag:false
  }
]

const Locations = () => {
  let locations = useRef([])

  let [location, setLocation] = useState(false)

  const changeLocation = () => {
    setLocation(!location)
  }

  return (
    <div className="locations">
      <div className="location-content">
        <div className="location-text-block">
          <TextBlock value={'locations'} color={'#fff'} blockSize={'8px'}/>
        </div>

        {
          locationsArray.map( (elem, i) => {
            return (
              <motion.div ref={ref => locations.current[i] = ref} className="location-heading" key={i}
              animate={location===elem.flag?"open":"close"}
              variants={{
                open:{
                  pointerEvents:"none",
                  opacity:1,
                },
                close:{
                  pointerEvents:'all',
                  opacity:0.3,
                }
              }}
              whileHover={'whileHover'}
              initial={'initial'}            
              onClick={changeLocation}
              >
                <motion.div className="arrow"
                variants={{
                  initial:{
                    scale: 0,
                  },
                  whileHover: {
                    scale: 1,
                    transition:{
                      ease:[0.65, 0, 0.35, 1],
                      duration:.3,
                    }
                  }
                }}
                >
                  <img src={rightArr} alt="" style={{width:'100%', height:'100%', objectFit:'cover', filter:'invert(1)'}}/>
                </motion.div>

                <motion.h1
                variants={{
                  initial: {
                    x: -25,
                  },
                  whileHover: {
                    x: 0,
                    transition:{
                      ease:[0.65, 0, 0.35, 1],
                      duration:.3,
                    }
                  }
                }}
                >
                  {elem.text}
                </motion.h1>
              </motion.div>
            )
          } )
        }

        {
          locationsMobile.map( (elem,i) => {
            return (
              <div className="location-item-mobile">
                <img src={elem.img} alt="" />
                <h1>{elem.title}</h1>
                <div className="address">
                  <h2>{elem.address.street}</h2>
                  <h2>{elem.address.area}</h2>
                  <h2>{elem.address.city}</h2>
                </div>
                <div className="contact">
                  <a href="">{elem.links.email}</a>
                  <a href="">{elem.links.phone}</a>
                </div>
              </div>
            )
          } )
        }

        {
         locationsMobile.map( (elem,i) => {
          return (
            <motion.div className="fixed-location-address"
            key={i}
            variants={{
              initial:{
                opacity: 0,
                pointerEvents:'none'
              },
              open:{
                opacity: 1,
                pointerEvents:"all"
              },
            }}
            initial={'initial'}
            animate={location===elem.flag?"open":"initial"}
            >
              <h2>{elem.address.street}</h2>
              <h2>{elem.address.area}</h2>
              <h2>{elem.address.city}</h2>
            </motion.div>
          )
         } ) 
        }

      </div>
      <div className="vignette">
        <div className="vignette-inner">
          {
            vimImages.map( (elem,i) => {
              return (
                <motion.img
                 animate={location===elem.flag?"open":"close"}
                 variants={{
                  open:{
                    opacity:1,
                  },
                  close:{
                    opacity: 0
                  }
                 }}
                 src={elem.img} alt="" key={i}  style={{width:'100%', height:"100%", objectFit:'cover', position:'absolute',left:"0",top:"0"}} />
              )
            }  )
          }
        </div>
      </div>
      <div className="fixed-contact location-fixed-contact">
            <a href="">hello@pixelflakes.com</a>
            <a href="">+44 (0)20 7112 9355</a>
        </div>
    </div>
  )
}