const Team = () => {

  let [activeFrame, setActiveFrame] = useState(99)

  let [leadersActive, setLeadersActive] = useState(false)

  const [text, setText] = useState('View Leaders')

  let handleClick = () => {
    if(text=='View team'){
        setText("View leaders")
    }else{
        setText('View team')
    }
    setLeadersActive(!leadersActive)
  }

  return (
    <div className='team-section'>
        <div className="mobile-scroll-text">
            <h1>Drag to Explore team</h1>
        </div>
        <MobileOnly>
            <div className="team-section-inner" >
                <MembersStack start={0} activeFrame={activeFrame} setActiveFrame={setActiveFrame}
                    leadersActive={leadersActive}/>

                <Frame leadersActive={leadersActive} setLeadersActive={setLeadersActive}/>
                
                <MembersStack start={14} activeFrame={activeFrame} setActiveFrame={setActiveFrame}
                leadersActive={leadersActive}/>
            </div>
        </MobileOnly>
        <div className="mobile-mode" onClick={handleClick}>
            <Button title={text} textColor={'#121212'} initial={'#fff'} invert={0} up={"#fff"} down={'#121212'}
            lineColor={'#121212'} fontWeight={900} fontFamily={'a2'} letterSpacing={'0.2px'} borderColor={'#121212'}
            />
        </div>
    </div>
  )
}

export default Team
  
const MobileOnly = ({children}) => {

    return (
        <motion.div className="mobile-scroll" drag="x"
         dragConstraints={{
            left: -26.5*(window.innerWidth*0.02+180),
            right: 0,
         }}>
            {children}
        </motion.div>
    )
}
  
