import React, { useEffect } from 'react'
import './index.css'
import {motion} from 'framer-motion'
import { membersdata } from '../team'
import Member from '../member/member'

const MembersStack = ({ start, activeFrame, setActiveFrame, leadersActive }) => {
    
  return (
      <motion.div className='stack-members'>

        {
          [...Array(14)].map( (_,i) => {
            let index = start + i + 1 
            return  <Member photo={`./members/member${index}.webp`}
                            leader={membersdata[index - 1].leader} 
                            index={index - 1}
                            leadersActive={leadersActive}
                            activeFrame={activeFrame}
                            setActiveFrame={setActiveFrame}
                    />
          } )
        }

      </motion.div>
  )
}

export default MembersStack
