import React, { useEffect, useRef } from 'react'
import './member.css'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import bezier from 'bezier-easing'
import { membersdata } from '../team'

const Member = ( { photo, leader, index, activeFrame, setActiveFrame, leadersActive } ) => {
  let member = useRef(null)

  let ease = bezier(0.83, 0, 0.17, 1)

  let changeFrame = () => {
    if(activeFrame!==index){
      gsap.to('.member-frame',{
        opacity: 0,
        pointerEvents: 'none',
        duration: .4,
        ease: ease,
      })
      gsap.set('.team-poster',{
        opacity: 0,
        duration: .4,
        ease: ease,
      })
      gsap.from( `.frame-number-${index} .content h2`,{
        opacity: 0,
        top: '30px',
        stagger: 0.07,
        duration: .6,
        ease: ease
      })
      gsap.to(`.frame-number-${index}`,{
          opacity: 1,
          pointerEvents: 'all',
          duration: .4,
          ease: ease
      })
      setActiveFrame(index)
    }
  }

  if(leadersActive==true && leader==false){
    gsap.to(member.current,{
      opacity: 0.1,
      pointerEvents: 'none'
    })
  }else{
    gsap.to(member.current,{
      opacity: 1,
      pointerEvents: 'all'
    })
  }

  return (
    <div className='member' ref={member} onMouseEnter={changeFrame}
    >
        <img src={photo} alt="" />
        <div className="mobile-member-content">
          <h2 className='name'>{membersdata[index].name}</h2>
          <h2 className='role'>{membersdata[index].occupation}</h2>
        </div>
    </div>
  )
}

export default Member