import React, { useEffect, useRef, useState } from 'react'
import './frame.css'
import { membersdata } from '../team'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import Button from '../../../button/button'

const Frame = ({leadersActive, setLeadersActive}) => {

  const ref = useRef(null)

  const [text, setText] = useState('View Leaders')

  useEffect(()=>{

    gsap.registerPlugin(ScrollTrigger)

    ScrollTrigger.create({
        trigger: '.stack-members',
        start: 'top top',
        end: 'bottom bottom',
        pin: ref.current,
        invalidateOnRefresh: true,
        // markers: true,
        pinnedContainer: ref.current,
    })

  },[])

  let handleClick = () => {
    if(text=='View team'){
        setText("View leaders")
    }else{
        setText('View team')
    }
    setLeadersActive(!leadersActive)
  }

  return (
        <div className='frame-wrap' ref={ref}>
            <div className="team-poster">
                <h1 className='poster-h1'>
                    <span>Team</span>
                    <span style={{fontSize:'20px'}}>29</span>
                </h1>
            </div>
            {
                [...Array(28)].map( (_,i) => {
                    return (
                        <div className={`member-frame frame-number-${i}`} key={i}>
                            <div className="member-frame-inner">
                                <img src={`./members/member${i+1}.webp`} alt="" />
                                <div className="content">
                                    <h2 className='name'>{membersdata[i].name}</h2>
                                    <h2 className='role'>- {membersdata[i].occupation}</h2>
                                </div>
                            </div>
                        </div>
                    )
                } )
            }
            <div className="team-mode" onClick={handleClick}>
                <Button title={text} textColor={'#121212'} initial={'#fff'} invert={0} up={"#fff"} down={'#121212'}
                lineColor={'#121212'} fontWeight={900} fontFamily={'a2'} letterSpacing={'0.2px'} borderColor={'#121212'}
                />
            </div>
        </div>
  )
}

export default Frame
